import createActionCreator, {
	IStandardAction,
} from '../utils/createActionCreator'
import { Dispatch } from 'redux'
import api from '../utils/api'
import { IAddress } from '../reducers/addresses'
import { IDate } from '../models/date'

export const SET_USER = 'SET_USER'
export type SetUserAction = IStandardAction<{
	id: string
	email: string
	token: string
	cardIssuer: string
	cardLast4: string
}>
const setUser = createActionCreator(
	SET_USER,
	(id: string, email: string, token: string, cardIssuer: string, cardLast4: string) => ({
		id,
		email,
		token,
		cardIssuer,
		cardLast4,
	}),
)

export const loginWithEmailPassword = (email: string, password: string) => (
	dispatch: Dispatch<any>,
) => {
	dispatch(setUserLoading({})) // @TODO: remove dummy object {}
	api.loginWithEmailPassword(email, password)
		.then((data) => {
			dispatch(setUser(
				data.id,
				data.email,
				data.token,
				data.cardIssuer,
				data.cardLast4,
			))
		})
		.catch((error) => {
			console.error(error)
			dispatch(unsetUserLoading({}))
		})
}

export const autoLoginWithEmailPassword = (email: string, password: string) => (
	dispatch: Dispatch<any>,
) => {
	dispatch(setUserLoading({}))
	api.loginWithEmailPassword(email, password)
		.then((data) => {
			dispatch(setUser(
				data.id,
				data.email,
				data.token,
				data.cardIssuer,
				data.cardLast4,
			))
		})
		.catch((error) => {
			console.error(error)
			dispatch(unsetUserLoading({}))
		})
}

export const registerWithEmailPassword = (email: string, password: string) => (
	dispatch: Dispatch<any>,
) => {
	dispatch(setUserLoading({}))
	api.registerWithEmailPassword(email, password)
		.then((data) => {
			dispatch(setUser(
				data.id,
				data.email,
				data.token,
				data.cardIssuer,
				data.cardLast4,
			))
		})
		.catch((error) => {
			console.error(error)
			dispatch(unsetUserLoading({}))
		})
}

export const UNSET_USER = 'UNSET_USER'
export type UnsetUserAction = IStandardAction<{}>
const unsetUser = createActionCreator(
	UNSET_USER,
	() => ({}),
)

export const logout = () => (
	dispatch: Dispatch<any>,
) => {
	dispatch(unsetUser({})) // @TODO: remove dummy object {}
}

export const logoutAndPreventAutoSignIn = () => (
	dispatch: Dispatch<any>,
) => {
	dispatch(unsetUser({}))

	// @ts-ignore
	if (navigator.credentials && navigator.credentials.preventSilentAccess) {
		// @ts-ignore
		navigator.credentials.preventSilentAccess()
	}
}

export const SET_USER_LOADING = 'SET_USER_LOADING'
const setUserLoading = createActionCreator(
	SET_USER_LOADING,
	() => ({}),
)

export const UNSET_USER_LOADING = 'UNSET_USER_LOADING'
const unsetUserLoading = createActionCreator(
	UNSET_USER_LOADING,
	() => ({}),
)

export const SET_DEFAULT_ADDRESS = 'SET_DEFAULT_ADDRESS'
export type SetDefaultAddressAction = IStandardAction<null | IAddress>
export const setDefaultAddress = createActionCreator(
	SET_DEFAULT_ADDRESS,
	(address: null | IAddress) => address,
)

export const SET_DEFAULT_DATE = 'SET_DEFAULT_DATE'
export type SetDefaultDateAction = IStandardAction<null | IDate>
export const setDefaultDate = createActionCreator(
	SET_DEFAULT_DATE,
	(date: null | IDate) => date,
)

export const REMOVE_CARD = 'REMOVE_CARD'
export type RemoveCardAction = IStandardAction<{}>
const remove = createActionCreator(
	REMOVE_CARD,
	() => ({}),
)

export const removeCard = () => (
	dispatch: Dispatch<any>,
) => {
	api.removeCard()
	dispatch(remove({}))
}



