import { connect } from 'react-redux'
import { IState } from '../reducers'
import Home from '../components/Home'

const mapStateToProps = (state: IState) => ({
	basketItems: state.basket,
})
const mapDispatchToProps = {}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Home)



